import React from 'react'
import './styles/style.css'

let $ = require('jquery');

let hotTopic = require('./images/hotTopic.png');


//
class Headlines extends React.Component {
    componentDidMount(){
      setInterval(function () {
        $('.maquee').find("ul").animate({
          marginTop : "-30px"
        },500,function(){
          $(this).css({marginTop : "0px"}).find("li:first").appendTo(this);
        })
      },3000);
    }
    render() {
        return (
            <div>
              <div className="hotTopic clearfix">
                <div className="hotTopicLeft fl">
                  <img src={hotTopic} alt="hotTopic" />
                </div>
                <div className="hotTopicRight fl">
                  <div className="maquee">
                    <ul>
                      <li>
                        <span></span>
                      </li>
                      <li>
                        <span></span>828
                      </li>
                      <li>
                        <span></span>
                      </li>
                      <li>
                        <span></span>828
                      </li>
                      <li>
                        <span></span>
                      </li>
                    </ul>
                  </div>

                </div>
              </div>
            </div>
        )
    }
    clickHandler() {
      $('.waitingPopu').fadeIn(500);
      setTimeout(function(){
        $('.waitingPopu').fadeOut();
      },2000)
    }
}

export default Headlines



